import ParamsTable from '@/components/ParamsTable.vue';
import RequiresList from '@/components/RequiresList.vue';
export default {
  name: 'Headings',
  components: {
    RequiresList: RequiresList,
    ParamsTable: ParamsTable
  },
  data: function data() {
    return {
      preview: '<h1 class="is-h1">we must stand up</h1>',
      markup: "<h1>...</h1>\n<div class=\"is-h1\">...</div>",
      headingStyle: 'is-h1',
      headingsElement: 'h1',
      requires: [{
        name: 'array-magic()',
        type: 'function',
        link: '#scroll-to-array-magic'
      }, {
        name: 'get-font-size()',
        type: 'mixin',
        link: '#scroll-to-get-font-size'
      }, {
        name: 'inject-style()',
        type: 'mixin',
        link: '#scroll-to-inject-style'
      }],
      coreParams: [{
        name: '$base-font-size',
        type: 'number',
        default: '16'
      }, {
        name: '$base-line-height',
        type: 'number',
        default: '1.4'
      }],
      params: [{
        name: '$headings-font-family',
        type: 'string',
        default: 'sans-serif'
      }, {
        name: '$headings-line-height',
        type: 'number',
        default: '$base-line-height // 1.4'
      }, {
        name: '$headings-weight',
        type: 'string',
        default: '$is-bold // bolder'
      }, {
        name: '$headings-style',
        type: 'map'
      }, {
        name: '$headings-inline-small-style',
        description: 'for inline &lt;small&gt; elements ',
        type: 'map'
      }, {
        name: '$h1-style',
        type: 'map'
      }, {
        name: '$h2-style',
        type: 'map'
      }, {
        name: '$h3-style',
        type: 'map'
      }, {
        name: '$h4-style',
        type: 'map'
      }, {
        name: '$h5-style',
        type: 'map'
      }, {
        name: '$h6-style',
        type: 'map'
      }, {
        name: '$giga-style',
        type: 'map'
      }, {
        name: '$mega-style',
        type: 'map'
      }, {
        name: '$kilo-style',
        type: 'map'
      }, {
        name: '$_headings-vertical-rhythm',
