      }, {
        name: '$h3-spacing',
        type: 'array',
        default: '$h2-spacing'
      }, {
        name: '$h4-spacing',
        type: 'array',
        default: '$h3-spacing'
      }, {
        name: '$h5-spacing',
        type: 'array',
        default: '0 0 ($_headings-vertical-rhythm / 2) 0'
      }, {
        name: '$h6-spacing',
        type: 'array',
        default: '$h5-spacing'
      }, {
