      } else {
        this.markup = "<".concat(this.headingsElement, " class=\"").concat(val, "\">...</").concat(this.headingsElement, ">\n<div class=\"").concat(val, "\">...</div>");
      }

      this.preview = "<".concat(this.headingsElement, " class=\"").concat(val, "\">we must stand up</").concat(this.headingsElement, ">");
    }
  }
};